import React from 'react'
import DataGrid, { EditorProps, TextEditor } from 'react-data-grid'
import SelectEditor from './selectEditor'

interface Row {
  id: number;
  title: string;
  // editor?: typeof TextEditor;
}

const states = [
  { label: 'Indiana', value: 'IN' },
  { label: 'Illinois', value: 'IL'},
]

const columns = [
  { key: 'id', name: 'ID' },
  { key: 'title', name: 'Title', editor: TextEditor },
  { key: 'state', name: 'State', editor: (p: EditorProps<Row>) => (<SelectEditor {...p} options={states} />) }
];

const rows = [
  { id: 0, title: 'Example' },
  { id: 1, title: 'Demo' }
];

const rowKeyGetter = (row: Row): number => {
  return row.id
}

export const ReactGrid = () => {
  console.log(columns, rows)

  return (
    <DataGrid columns={columns} rows={rows} />
  )
}
